body{
    background-color: rgb(9, 113, 121);
    margin: 0;
    font-family: sans-serif;
    text-align: center;
}
h1{
    color: rgb(126, 61, 104);
    margin: 0px;
    margin-bottom: 15px;
}

.input-section{
background-color: rgb(240, 140, 140);
padding: 18px;
border-radius: 10px;
box-shadow: 0 3px 4px rgb(80, 80, 80);
}
.input-section ::placeholder{
    color: tomato;
}
.input-section input{
border-radius: 5px;
border: 1px solid rgb(228, 228, 228);
width: 100%;
padding: 5px;
box-sizing: border-box;
background-color:rgb(223, 223, 223) ;
outline: none;
color: tomato;
}

ul{
    text-align: left;
    padding: 0;
   
}
ul li{
    display: flex;
    list-style-type: none;
    background-color: rgb(223, 223, 223);
    padding: 15px;
    border-radius: 5px;
    margin-bottom: 10px;
    color: tomato;
    box-shadow: 0 3px 4px rgb(80, 80, 80);
}
button{
    padding: 2px;
    margin: 5px;
}

.todo-container{
    width: 350px;
    margin: auto;
    text-align: center;
    margin-top: 120px;

}
ul li i{  
    text-align: right;
    cursor: pointer;
    flex: 1;
}